.container {
    display: flex;
    /* width: 100%; */
    background-color:#c9c9c937;
    border: 1px solid #cccccc4c;
    margin: 50px 50px 0px 50px;
  }
  
  .tasksContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 50px;
  }
  
  .taskItem {
    flex-basis: calc(33.33% - 50px);
    margin: 10px;
  }
  
  .imageColumn {
    flex: 3;
    text-align: center;
    padding: 30px;
  }
  
  .userImage {
    width: 400px;
    height: 250px;
    object-fit: contain;
  }
  
  .infoColumn {
    flex: 9;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 20px;
    margin-bottom: 20px;
  }
  
  .row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 5px;
  }
  
  .userName {
    flex-basis: 100%;
  }
  
  .jobTitle {
    flex-basis: 50%;
    text-align: left;
  }
  
  .email {
    flex-basis: 50%;
    text-align: right;
  }
  
  .skills {
    display: flex;
  }
  
  .skillBox {
    background-color: #A2A3A0;
    border: 1px solid #cccccc4c;
    border-radius: 25px;
    padding: 4px 8px;
    margin-right: 5px;
    transition: background-color 0.3s ease;
  }
  
  .skillBox:hover {
    background-color: #4caf50;
    cursor: pointer;
  }
  
  .deleteButton {
    padding: 10px 20px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .taskRow {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .editButtonContainer {
    cursor: pointer;
  }
  
  .editIcon {
    font-size: 20px;
    color: #007bff; /* Change the color to your preference */
  }
  
  /* Overlay styles */
  .updateTaskOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6); /* Semi-transparent black */
    z-index: 1; /* Place the overlay on top of everything else */
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  /* Popup styles */
  .updateTaskPopup {
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    padding: 20px 50px 20px 50px;
    width: 900px;
  }