.formContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 60%;
    margin: 0 auto;
    padding: 30px;
    border: 1px solid #ccc;
    border-radius: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .mainForm {
    width: 80%;
  }
  
  .addNewTaskHeading {
    text-align: center;
    font-size: 30px;
    margin-bottom: 30px;
    color: #4caf50;
  }
  
  .task-form {
    display: flex;
    flex-direction: row;
  }
  
  .left-column {
    width: 65%;
  }
  
  .right-column {
    width: 35%;
    padding-left: 40px;
  }
  
  .alignSameLine {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 25px;
  }
  
  .alignSameLine label {
    width: 150px;
    height: 30px;
  }
  
  .titleInputField {
    width: 70%;
  }
  
  .descriptionTextArea {
    width: 70%;
    min-height: 150px;
  }
  
  .completedCheckBox {
    width: 50px;
    background-color: red;
  }
  
  .alignEmailItems {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }
  
  .emailInput {
    height: 25px;
    width: 70%;
  }
  
  .emailList {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  
  .eachEmail {
    margin-right: 10px;
    margin-bottom: 5px;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 20px;
    /* background-color: #f7f7f7; */
  }
  
  .removeEmailButton {
    background-color: transparent;
    border: 0px;
    margin-left: -5px;
    cursor: pointer;
  }
  
  .submit-button-row {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
  }
  
  .submit-button-row button {
    padding: 10px 20px;
    background-color: #4CAF50;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .submit-button-row button:hover {
    background-color: #255426;
  }
  
  .toggle-switch {
    width: 60px;
    height: 30px;
    border-radius: 15px;
    background-color: #ccc;
    cursor: pointer;
    position: relative;
  }
  
  .toggle-switch.active {
    background-color: #007bff;
  }
  
  .toggle-switch-handle {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: #fff;
    position: absolute;
    top: 3px;
    left: 3px;
    transition: left 0.2s;
  }
  
  .toggle-switch.active .toggle-switch-handle {
    left: calc(100% - 27px);
  }